
import {mapGetters, mapMutations} from 'vuex';
import PopupPicker from 'vux/src/components/popup-picker/index.vue'

import Number from 'base/number/number';
import {setListMixin} from 'common/js/mixin';
import {changeShopNumber} from 'common/js/util';
import {getCurrentTime} from 'api/user';
import {orderCreate} from 'api/pay';
import {ERR_OK} from 'api/config';
export  default {
data(){
return {
remark: '',
alert: {
state: true,
text: '',
cancelBtnText: ''
},
date: {
title: "",
list: [],
value: []
},
time: {
title: "",
list: [[
{name: '9:00  11:00', value: '9:00  11:00'},
{name: '11:00  13:00', value: '11:00  13:00'},
{name: '13:00  15:00', value: '13:00  15:00'},
{name: '15:00  17:30', value: '15:00  17:30'}
]],
value: ['9:00  11:00']
}
}
},
components: {
PopupPicker,
Number
},
computed: {
totalPrice(){
let totalPrice = 0;
this.shopList.forEach((item) => {
totalPrice += item.number * item.price;
});
return totalPrice;
},
address(){
return this.currentAddress || {}
},
...mapGetters([
'shopList',
'currentAddress'
])
},
mixins: [setListMixin],
created(){
this._getCurrentTime();
},
methods: {
onCreatePayClick(){
if (this.currentAddress != null) {     //
if (this.shopList.length !== 0) {   //
this.$loading.show('');
let data = this.submitData();
orderCreate(this.urlType, data).then((ops) => {
if (ops.code === ERR_OK) {
let route = this.$route;
let top = route.query.top || '';
this.$loading.hide();
this.$router.push({
name: 'payChose',
query: {totalPrice: this.totalPrice, type: route.params.name, top},
params: {id: ops.data.id}
});
return;
}
this.$loading.hide();
this.$msg.setShow(ops.msg);
}).catch(() => {
this.$loading.hide();
this.$msg.setShow('');
})
}
else {
this.$alert('')
}
}
else {
this.$alert('')
}
},
submitData(){
let items = [];
this.shopList.map((item) => {
items.push({[`${this.urlType}Product`]: {id: item.id}, count: item.number});
});
let date = this.date.value[0].replace(/(||)|||/g, ',');
date = date.split(',');
date.shift();
date.pop();
let time = this.time.value[0];
time = time.match(/[0-9]+(:|)[0-9]+/)[0];
let deliveryDate = `${date[0]}-${date[1]}-${date[2]} ${time}`;
let address = this.currentAddress;
let data = {
name: address.consignee,
phone: address.phone,
province: address.province,
city: address.city,
area: address.area,
address: address.address,
deliveryDate,
remark: this.remark,
type: this.type||'',
items
};
return data;
},
toAddress(){
this.$router.push({path: '/my/address', query: {pay: true}});
},
numChange(number, id){
let shopList = changeShopNumber(this.shopList, number, id);
this.setShopList(shopList);
this.setList(shopList, this.$route.params.name);
if (shopList.length === 0) {  //0
this.$router.back();
}
},
_getCurrentTime(){
this.$loading.show() ;
getCurrentTime().then((ops) => {
if (ops.code === ERR_OK) {
this.$loading.hide();
this.timeFormat(ops.data)
}
})
},
timeFormat(data){
let time = new Date(data);
let year = time.getFullYear();
let month = time.getMonth() + 1;
let date = time.getDate();
month = month < 10 ? '0' + month : month;
date = date < 10 ? '0' + date : date;
let arr = [];
let format = `${year}${month}${date}`;
arr.push({name: format, value: format});
for (let i = 1; i < 3; i++) {
let text = (i === 1) ? '' : '';
let format = `${text}${year}${month}${date + i}`;
arr.push({name: format, value: format});
}
this.date.value = [arr[0].value];
this.date.list = [arr];
},
...mapMutations({
setShopList: 'SET_SHOP_LIST'
})
}

}



